<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName
              }}<i @click="removeCategoryName">×</i>
            </li>
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}<i @click="removeKeyword">×</i>
            </li>
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1]
              }}<i @click="removeTrademark">×</i>
            </li>
            <li
              class="with-x"
              v-for="(attrValue, index) in searchParams.props"
              :key="index"
            >
              {{ attrValue.split(":")[1]
              }}<i @click="removeAttrValue(index)">×</i>
            </li>
          </ul>
        </div>

        <!--selector 品牌-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="changeOrder('1')">
                  <a
                    >综合<i
                      v-show="isOne"
                      class="iconfont"
                      :class="{
                        'icon-jiantou_xiangshang': isAsc,
                        'icon-jiantou_xiangxia': isDesc,
                      }"
                    ></i
                  ></a>
                </li>
                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                  <a
                    >价格<i
                      v-show="isTwo"
                      class="iconfont"
                      :class="{
                        'icon-jiantou_xiangshang': isAsc,
                        'icon-jiantou_xiangxia': isDesc,
                      }"
                    ></i
                  ></a>
                  <!-- jiantou_xiangshang -->
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-list">
            <ul class="yui3-g">
              <li
                class="yui3-u-1-5"
                v-for="(goods, index) in goodsList"
                :key="goods.id"
              >
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link :to="`/detail/${goods.id}`">
                      <img v-lazy="goods.defaultImg" />
                    </router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>{{ goods.price }}.00</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <router-link
                      :to="`/detail/${goods.id}`"
                      :title="goods.title"
                      >{{ goods.title }}</router-link
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页 pageNo:当前页，pageSize：每页显示数据，total：全部数据，continues：每页相隔显示个数-->
          <Pagination
            :pageNo="searchParams.pageNo"
            :pageSize="searchParams.pageSize"
            :total="total"
            :continues="5"
            @getPageNo="getPageNo"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { mapState, mapGetters } from "vuex";
import Pagination from "../../components/Pagination/index.vue";
export default {
  name: "Search",
  components: {
    SearchSelector,
    Pagination,
  },
  data() {
    return {
      searchParams: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        categoryName: "",
        keyword: "",
        order: "1:asc", //降序
        pageNo: 1,
        pageSize: 10,
        props: [],
        trademark: "",
      },
    };
  },
  beforeMount() {
    //请求前，整理传递参数
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },
  mounted() {
    this.getData();
  },
  // getters
  computed: {
    // ...mapState({
    //   goodsList: (state) => state.search.searchList.goodsList,
    // }),
    // ...mapGetters(["search/goodsList"]),
    ...mapGetters("search", ["goodsList"]),
    isOne() {
      return this.searchParams.order.indexOf("1") != -1;
    },
    isTwo() {
      return this.searchParams.order.indexOf("2") != -1;
    },
    isAsc() {
      return this.searchParams.order.indexOf("asc") != -1;
    },
    isDesc() {
      return this.searchParams.order.indexOf("desc") != -1;
    },
    //search总数据
    ...mapState({
      total: (state) => state.search.searchList.total,
    }),
  },
  methods: {
    getData() {
      this.$store.dispatch("search/getSearchInfo", this.searchParams);
    },
    removeCategoryName() {
      // undefined 不用重请求
      this.searchParams.categoryName = undefined;
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      this.getData();
      if (this.$route.params) {
        this.$router.push({ name: "search", params: this.$route.params });
      }
    },
    removeKeyword() {
      this.searchParams.keyword = undefined;
      this.getData();
      this.$bus.$emit("clear");
      if (this.$route.query) {
        this.$router.push({ name: "search", query: this.$route.query });
      }
    },
    trademarkInfo(trademark) {
      this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
      this.getData();
    },
    removeTrademark() {
      this.searchParams.trademark = undefined;
      this.getData();
    },
    attrInfo(attr, attrValue) {
      // console.log(attr, attrValue);
      //整理格式 ["属性ID:属性值:属性名"]
      let props = `${attr.attrId}:${attrValue}:${attr.attrName}:`;
      //数组去重
      if (this.searchParams.props.indexOf(props) == -1) {
        this.searchParams.props.push(props);
        this.getData();
      }
    },
    removeAttrValue(index) {
      this.searchParams.props.splice(index, 1);
      this.getData();
    },
    changeOrder(flag) {
      // flag形参：标记，代表用户点击的是 1 | 2
      // console.log(flag);
      let originOrder = this.searchParams.order;
      let originFlag = this.searchParams.order.split(":")[0];
      let originSort = this.searchParams.order.split(":")[1];
      let newOrder = "";
      //判断升降排序
      if (flag == originFlag) {
        newOrder = `${originFlag}:${originSort == "desc" ? "asc" : "desc"}`;
        // console.log(newOrder);
      } else {
        // newOrder = `${originFlag}:${originSort == "desc" ? "asc" : "desc"}`;
        newOrder = `${flag}:${"desc"}`; //2:desc
      }
      this.searchParams.order = newOrder;
      this.getData();
      // console.log(originFlag, originSort);
    },
    //当前在第几页
    getPageNo(pageNo) {
      // console.log(pageNo);
      this.searchParams.pageNo = pageNo;
      this.getData();
    },
  },
  watch: {
    //路由信息发生变化
    $route(newValue, oldValue) {
      // 再次请求参数
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      // 重请求
      this.getData();
      //请求完毕，清空对象
      this.searchParams.category1Id = "";
      this.searchParams.category2Id = "";
      this.searchParams.category3Id = "";
      // console.log(this.searchParams);
      if (this.$route.params) {
        this.$router.push({
          name: "search",
          params: this.$route.params,
          query: this.$route.query,
        });
      }
    },
  },
};
</script>

